import React, { useState } from 'react'
import './Banner.css'
import { Button } from "@material-ui/core";
import Search from './Search';
import { useHistory } from 'react-router-dom';
function Banner() {
    //adding state for date picker
    const history = useHistory();
    const [showSearch, setShowSearch] = useState(false); //to keep track if we selected "Search Dates"

    return (
        <div className='banner'>
            <div className='banner__search'>
                {showSearch && <Search />}
                <Button onClick={() =>
                    setShowSearch(!showSearch)}
                    className='banner__searchButton'
                    variant='outlined'>
                    {showSearch ? "Hide" : //if showsearch is true, then show hide else show search date
                        "Search Dates"}
                </Button>
            </div>
            <div className='banner__info'>
                <h1>Get out and stretch your imagination</h1>
                <h5>
                    Plan a different kind of getway to uncover the hidden gems near you.
                </h5>
                <Button onClick={() => history.push('/search')}
                    variant='outlined'>Explore Nearby</Button>
            </div>
        </div>
    )
}

export default Banner
